import React from "react";
import { TFeature } from "@/types";
import { EyeClosedIcon, SparklesIcon, StarIcon } from "lucide-react";

export interface IFeaturesSectionProps {
  id: number;
  __component: string;
  title: string;
  description: string;
  heading: string;
  subHeading: string;
  features?: TFeature[] | null;
}

function getIcon(name: string) {
  switch (name) {
    case "EYE_ICON":
      return <EyeClosedIcon className="w-12 h-12 mb-4 text-gray-900" />;
    case "SPARKLES_ICON":
      return <SparklesIcon className="w-12 h-12 mb-4 text-gray-900" />;
    case "STAR_ICON":
      return <StarIcon className="w-12 h-12 mb-4 text-gray-900" />;
    default:
      return null;
  }
}

const styles = {
  container: "flex-1",
  section: "container px-8 py-6 mx-auto md:px-8 lg:py-24",
  grid: "grid gap-8 md:grid-cols-3",
  featureCard: "flex flex-col items-center text-center",
  icon: "w-12 h-12 mb-4 text-gray-900",
  heading: "mb-4 text-2xl font-bold",
  description: "text-gray-500",
};

export function FeaturesSection({ data }: { data: IFeaturesSectionProps }) {
  if (!data?.features) return null;
  return (
    <div>
      <div className="container mx-auto max-w-4xl px-8 py-6 text-center md:px-6 lg:py-6">
        <h2 className="text-md text-black mb-4">{data.heading}</h2>
        <h3 className="text-md text-brand-dark-grey mb-4">{data.subHeading}</h3>
        <p className="text-brand-dark-grey mb-6">{data.description}</p>
      </div>
      <div className="flex-1">
        <section className="container px-8 py-6 mx-auto md:px-8 lg:py-12">
          <div className="grid gap-8 md:grid-cols-3">
            {data.features.map((item: TFeature) => (
              <div className="flex flex-col items-center text-center" key={item.id}>
                {getIcon(item.icon)}
                <h2 className="mb-4 text-2xl font-bold">{item.heading}</h2>
                <p className="text-brand-dark-grey">{item.subHeading}</p>
              </div>
            ))}
          </div>
        </section>
      </div>
    </div>
  );
}