import { BlocksContent, TImage } from "@/types";
import { BlockRenderer } from "./block-renderer";
import { StrapiImage } from "./strapi-image";


export interface IImageWithTextProps {
    id: number;
    documentId: string;
    __component: string;
    heading: string;
    subHeading: string;
    description: string;
    perks: BlocksContent;
    image: TImage;
}

export function ImageWithText({ data }: { data: IImageWithTextProps }) {
    if (!data) return null;
    
    const { heading, subHeading, description, perks, image } = data;
    return (
        <section className="px-8 pb-6 mx-auto md:px-6 lg:py-2 bg-brand-cream">
            <div className="container mx-auto max-w-6xl">
                <div className="rounded-xl border bg-white my-4 py-6 px-10 shadow-sm transition-shadow hover:shadow-md">
                    <div className="flex flex-col lg:flex-row items-center gap-8">
                        {/* Text Section */}
                        <div className="w-full lg:w-1/2 py-8 max-w-xl">
                            <h2 className="text-md text-black mb-4">{heading}</h2>
                            <h3 className="text-md text-brand-dark-grey mb-4">{subHeading}</h3>
                            <p className="text-brand-dark-grey mb-6">{description}</p>
                            <div className="prose prose-lg text-gray-700">
                                <BlockRenderer content={perks} />
                            </div>
                        </div>
                        {/* Image Section */}
                        <div className="w-full py-6 lg:w-1/2">
                            <StrapiImage
                                src={image.url}
                                alt={image.alternativeText || "Image"}
                                className="w-full h-auto object-cover rounded-lg shadow-lg max-h-96"
                                height={384}
                                width={400}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}